<template>
  <div>
    <div class="left-sidebar">
      <div class="work-table">
        <span class="work-table-span">工作表</span>
        <div class="work-table-switch">
          <span class="work-table-title nowrap" v-bind:title="$store.state.workTableActice.name"
            @click="dataViewAllFieldData()">{{$store.state.workTableActice.name}}</span>
          <i class="float-right work-table-more el-icon-sort" title="工作表切换" @click="switchWorkTable()"></i>
        </div>
      </div>
      <div class="search-fields-input">
        <div class="search-areas-of-add">
          <el-input placeholder="请输入需要搜索的字段" prefix-icon="el-icon-search" v-model="searchFieldsInput"
            @input="fieldListFilterDebounce">
          </el-input>
        </div>
      </div>
      <div class="original-fields-list">
        <!--组件数据加载动画-->
        <div class="loading-box design-chart-loading">
          <div class="data-loading-animation"></div>
        </div>
        <ul>
          <li class="original-fields-li" v-for="(fieldItem,index) in canFieldList" :key="fieldItem.itemIndex">
            <div class="original-fields-item">
              <i class="el-icon-document"></i>
              <span class="original-fields-item-name nowrap"
                v-bind:title="fieldItem.newName">{{fieldItem.newName}}</span>
              <i class="iconfont icon-edit" @click="renameFieldItem(fieldItem)"></i>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <!--对话框 - 重命名可操作字段项-->
    <el-dialog title="字段别名编辑" :visible.sync="renameFieldItemDialog" width="400px" top="50px">
      <el-form :model="renameFieldForm">
        <el-form-item label="字段别名:" label-width="75px">
          <el-input v-model="renameFieldForm.newName" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="renameFieldItemDialog = false">取 消</el-button>
        <el-button type="primary" @click="renameFieldItemSubmit(renameFieldForm)">确 定</el-button>
      </div>
    </el-dialog>
    <!--对话框 - 数据查看-->
    <el-dialog :title="viewDataTitle" :visible.sync="showDataViewALLFieldsDialog" width="90%" top="50px">
      <el-table :data="$store.state.dataSetList" :height="dataAllTableHeightStyle" stripe border size="small"
        :header-cell-style="dataTableHeaderStyle">
        <el-table-column label="序号" width="70px">
          <template slot-scope="scope">{{scope.$index+1}}</template>
        </el-table-column>
        <el-table-column width="180" sortable v-for="(item, index) in tableHeader" :key="index" :label="item.newName"
          :prop="item.name" align="center" />
      </el-table>
    </el-dialog>
    <!--对话框 - 选择工作表-->
    <el-dialog title="选择工作表" :visible.sync="switchWorkTableDialog" width=" 400px" top="50px">
      <el-row>
        <div class="grid-content bg-purple">
          <el-input v-model="searchWorkTableInput" placeholder="请输入需要搜索的工作表名称" @input="workTableFilterDebounce">
          </el-input>
          <ul class="work-table-list">
            <li v-for="(fieldItem,index) in searchWorkTableList" :key="index">
              <div class="original-fields-item"
                v-bind:class="fieldItem.name === workTableActice.name ? 'work-table-active' : ''"
                @click="workTableActice = fieldItem">
                <i class="el-icon-notebook-2"></i>
                <span class="original-fields-item-name nowrap" v-bind:title="fieldItem.name">
                  {{fieldItem.name}}
                </span>
              </div>
            </li>
          </ul>
        </div>
      </el-row>
      <div slot="footer" class="dialog-footer">
        <el-button @click="switchWorkTableDialog = false">取 消</el-button>
        <el-button type="primary" @click="switchWorkTableSubmit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import {
    debounce
  } from 'lodash'

  export default {
    name: 'columnList',
    components: {},
    data() {
      return {
        renameFieldForm: {
          name: '',
          newName: ''
        },
        dataTableHeaderStyle: {
          background: '#F5F7FA',
          color: '#606266'
        },
        tableHeader: [],
        dataAllTableHeightStyle: document.documentElement.clientHeight - 230,
        renameFieldItemDialog: false,
        showDataViewALLFieldsDialog: false,
        switchWorkTableDialog: false,
        searchFieldsInput: '',
        searchWorkTableInput: '',
        searchWorkTableList: [],
        workTableActice: {},
        canFieldList: JSON.parse(JSON.stringify(this.$store.state.fieldList)),
        viewDataTitle: '',

      }
    },

    //(创建后)
    created() {

      this.fieldListFilter();

    },
    //钩子(载入后)
    mounted() {

      this.getFieldsListHttp(this.$store.state.workTableActice);

    },
    computed: {
      //计算侦听
      //state状态更新且子组件状态未同步时,用computed

    },
    //函数列表
    methods: {

      //可操作字段列表过滤器
      fieldListFilter: _.debounce(function() {

        var allFieldList = JSON.parse(JSON.stringify(this.$store.state.fieldList));

        var matchItemList = [];

        var searchFieldsInput = this.searchFieldsInput;

        allFieldList.filter(function(item) {

          item = JSON.parse(JSON.stringify(item));

          if (item.newName.indexOf(searchFieldsInput) !== -1) {

            matchItemList.push(item);

          }

        })

        this.canFieldList = matchItemList;

      }, 800),

      fieldListFilterDebounce() {

        this.fieldListFilter();

      },

      //工作表列表过滤器
      workTableFilter: _.debounce(function() {

        var workTableList = [];

        var searchWorkTableInput = this.searchWorkTableInput;

        var searchWorkTableList = JSON.parse(JSON.stringify(this.$store.state.workTableList));

        searchWorkTableList.filter(function(item) {

          item = JSON.parse(JSON.stringify(item));

          if (item.name.indexOf(searchWorkTableInput) !== -1) {

            workTableList.push(item);

          }

        })

        this.searchWorkTableList = workTableList;

      }, 800),

      workTableFilterDebounce() {

        this.workTableFilter();

      },

      //已选字段列表过滤器
      selectedFieldsListFilter: _.debounce(function() {

        var workTableList = [];

        var searchWorkTableInput = this.searchWorkTableInput;

        var searchWorkTableList = JSON.parse(JSON.stringify(this.$store.state.workTableList));

        searchWorkTableList.filter(function(item) {

          item = JSON.parse(JSON.stringify(item));

          if (item.name.indexOf(searchWorkTableInput) !== -1) {

            workTableList.push(item);

          }

        })

        this.searchWorkTableList = workTableList;

      }, 800),

      selectedFieldsDebounce() {

        this.selectedFieldsListFilter();

      },

      //查看所有数据字段
      dataViewAllFieldData() {

        this.showDataViewALLFieldsDialog = true;

        this.tableHeader = this.getFieldsKeyList(this.$store.state.dataSetList);

        this.viewDataTitle = '查看数据 - [ ' + JSON.parse(JSON.stringify(this.$store.state.workTableActice.name))+' ]';

      },
      //打开工作表列表视图
      switchWorkTable() {

        this.switchWorkTableDialog = true;

        this.workTableActice = JSON.parse(JSON.stringify(this.$store.state.workTableActice));

        this.searchWorkTableList = JSON.parse(JSON.stringify(this.$store.state.workTableList));

      },
      //切换工作表
      switchWorkTableSubmit() {

        this.switchWorkTableDialog = false;

        this.$store.state.workTableActice = JSON.parse(JSON.stringify(this.workTableActice));

        this.getFieldsListHttp(this.$store.state.workTableActice);

        this.$store.state.defaultChartConfig.analysis_column = {

          data_0: [],
          data_1: [], //图层
          data_2: [], //X轴
          data_3: [], //对比
          data_4: [], //Y轴
          data_5: [], //次轴
          data_6: [], //筛选
        };

      },
      /* function - getFieldsKeyList
       * 从通用接口数据列表中获取所有的原始字段
       * @param {Array} dataRows -   通用接口数据列表
       * @return - fieldList 返回所有的原始字段
       */
      getFieldsKeyList(list) {

        var list = JSON.parse(JSON.stringify(list));

        var fieldList = [];

        var index = 0;

        for (var key in list[0]) {

          if (key !== "rn") {

            var fieldItem = {

              name: key,
              newName: key,
              itemIndex: index,
              type: "string" //string文本,number数值,日期date

            };

            fieldList.push(fieldItem);

            index++;

          }

        }

        return fieldList;

      },

      //获取操作字段列表请求
      getFieldsListHttp(httpObject, params) {

        var url = './static/store/' + httpObject.url;

        this.$http.get(url, {
          params: params
        }).then(function(res) {

          this.$store.state.dataSetList = res.data;

          this.$store.state.dataSetListLimit = res.data;

          var fieldList = this.getFieldsKeyList(res.data);

          this.$store.state.fieldList = JSON.parse(JSON.stringify(fieldList));

          // 响应成功回调
        }, function(res) {

          //console.log(res)

          // 响应错误回调

        })
      },
      //重命名可操作字段项
      renameFieldItem(item) {

        this.renameFieldItemDialog = true;

        this.renameFieldForm = JSON.parse(JSON.stringify(item));

      },

      //保存重命名可操作字段项
      renameFieldItemSubmit(item) {

        this.renameFieldItemDialog = false;

        this.$store.state.fieldList[this.renameFieldForm.itemIndex].newName = JSON.parse(JSON.stringify(item.newName));

      },

    },


  }
</script>
<style scoped>
  .search-areas-of-add {
    height: 32px;
    margin: 0;
    display: block;
    border-bottom: 1px solid #e8e8e8;
    position: relative;
    padding: 5px 10px 5px;
  }

  .el-icon-document {

    display: inline-block;
    float: left;
    height: 35px;
    line-height: 35px;
    font-size: 16px;
  }

  .work-table-list {

    height: 150px;
    margin: 5px 0 0;
  }

  .work-table-list li {
    overflow: hidden;
    margin: 0 0 3px 0;
  }

  .work-table-list li div {
    padding: 0 10px;
    cursor: pointer;
    border-radius: 4px;
  }

  .work-table-list li div:hover {
    background-color: #eee;
  }

  .work-table-list li div i {

    display: inline-block;
    float: left;
    height: 35px;
    line-height: 35px;
    font-size: 16px;
  }

  .original-fields-list {
    width: 190px;
    position: absolute;
    top: 135px;
    left: 0;
    bottom: 0px;
    z-index: 99;
    overflow: auto;
    overflow-x: hidden;
    padding: 0 5px 10px;
  }

  .work-table {
    height: 77px;
    margin: 0;
    display: block;
    position: relative;
    padding: 8px 10px 0px;
    border-bottom: 1px solid #e8e8e8;
  }

  .work-table-active,
  .work-table-active:hover {
    background-color: #984fff !important;
  }

  .work-table-active span,
  .work-table-active i {

    color: #fff;
  }

  .work-table-span {
    display: block;
    height: 35px;
    font-weight: 700;
    line-height: 35px;
    cursor: pointer;
  }

  .work-table-switch {

    display: block;
    height: 35px;
    color: #888;
    padding: 0 0 0 5px;

    line-height: 35px;
  }

  .work-table-title {
    display: inline-block;
    max-width: 140px;
    cursor: pointer;
    font-weight: 700;
    font-size: 14px;
    color: #999;
  }

  .work-table-switch:hover {
    background: #eee;
  }

  .work-table-more {

    display: inline-block;
    font-size: 15px;
    height: 35px;
    color: #999;
    cursor: pointer;
    margin-right: 5px;
    line-height: 35px;
    font-weight: 700;

  }
</style>



