import {
  Message
} from 'element-ui'

//表格解析模块导入
import DataTableParseModule from './DataTableParseModule'

//线状类图解析模块导入
import EchartsBarAndLineParseModule from './EchartsBarAndLineParseModule'

//散点图与气泡图解析模块导入
import EchartsScatterParseOptionParseModule from './EchartsScatterParseOptionParseModule'

//词云类图解析模块导入
import EchartsWordCloudParseModule from './EchartsWordCloudParseModule'

//饼图类图解析模块导入
import EchartsPieParseModule from './EchartsPieParseModule'


//矩形树图解析模块导入
import EchartsTreeMapParseModule from './EchartsTreeMapParseModule'

//仪表盘图解析模块导入
import EchartsGaugeParseModule from './EchartsGaugeParseModule'

//雷达图解析模块导入
import EchartsRadarParseModule from './EchartsRadarParseModule'

//进度条图解析模块导入
import EchartsProgressBarParseModule from './EchartsProgressBarParseModule'


const MainMutations = {

  //监听图表配置函数
  renderChartService(state, echarts) {

    //开启加载动画
    state.chartLoadingAnimation = true;

    var defaultChartConfig = JSON.parse(JSON.stringify(state.defaultChartConfig));

    //所选图表类型
    var selected_chart_type = defaultChartConfig.select_type;

    //图表主题
    var mainTheme = defaultChartConfig.chart_theme.main_theme;

    //颜色主题
    var colorTheme = defaultChartConfig.chart_theme.color_theme;

    //颜色主题配置
    var colorConfigList = [];

    for (var index in state.colorDefaultList) {

      var colorItem = state.colorDefaultList[index];

      if (colorItem.name === colorTheme) {

        colorConfigList = colorItem.color_list;

        break;

      }

    }

    if (selected_chart_type === 'C200') {

      var analysisFieldsList = DataTableParseModule.mutations.dataTableParseModuleOptionInit(state);

      state.tableHeader = analysisFieldsList.dataTableOption;
      
      state.dataSetListLimit = analysisFieldsList.dataSetList;

    } else {

      // 基于准备好的dom，初始化echarts实例
      let renderChartbox = echarts.init(document.getElementById('render-chart-box'));

      if (selected_chart_type === 'C210' || selected_chart_type === 'C215' ||
        selected_chart_type === 'C220' || selected_chart_type === 'C240' ||
        selected_chart_type === 'C211' || selected_chart_type === 'C241' ||
        selected_chart_type === 'C250' || selected_chart_type === 'C212' ||
        selected_chart_type === 'C351') {

        var echartsConfigOption = EchartsBarAndLineParseModule.mutations.barAndLineParseOptionInit(state,
          colorConfigList);

      } else if (selected_chart_type === 'C230' || selected_chart_type === 'C288') {

        var echartsConfigOption = EchartsPieParseModule.mutations.pieParseOptionInit(state, colorConfigList);

      } else if (selected_chart_type === 'C340' || selected_chart_type === 'C400') {

        var echartsConfigOption = EchartsWordCloudParseModule.mutations.wordCloudParseInit(state, colorConfigList);

      } else if (selected_chart_type === 'C410') {

        var echartsConfigOption = EchartsGaugeParseModule.mutations.gaugeParseOptionInit(state, colorConfigList);

      } else if (selected_chart_type === 'C600') {

        var echartsConfigOption = EchartsBarAndLineParseModule.mutations.liquidFillParseOptionInit(state,
          colorConfigList);

      } else if (selected_chart_type === 'C361') {

        var echartsConfigOption = EchartsTreeMapParseModule.mutations.treemapParseOptionInit(state, colorConfigList);

      } else if (selected_chart_type === 'C280' || selected_chart_type === 'C281') {

        var echartsConfigOption = EchartsScatterParseOptionParseModule.mutations.scatterParseOptionInit(state,
          colorConfigList);

      } else if (selected_chart_type === 'C350') {

        var echartsConfigOption = EchartsRadarParseModule.mutations.radarParseOptionInit(state, colorConfigList);

      } else if (selected_chart_type === 'C601') {

        var echartsConfigOption = EchartsProgressBarParseModule.mutations.progressBarParseOptionInit(state,
          colorConfigList);

      } else if (selected_chart_type === 'C666') {

        var echartsConfigOption = EchartsBarAndLineParseModule.mutations.pictorialBarParseOptionInit(state,
          colorConfigList);

      }

      //图表主题
      echartsConfigOption.echartOption.backgroundColor = mainTheme;

      console.log(echartsConfigOption.echartOption)

      setTimeout(() => {

        // 绘制图表
        renderChartbox.setOption(echartsConfigOption.echartOption, true);

        renderChartbox.resize();

      }, 300);

    }

    setTimeout(() => {

      state.chartLoadingAnimation = false;

    }, 500);

    console.log(selected_chart_type);

    Message({
      message: '图表渲染成功！',
      type: 'success'
    });

  },

}
export default MainMutations



