                    <span class="chart-config-left-label chart-config-item-span">刻度</span>
                    <el-switch class="chart-config-title-switch"
                      v-model="currChartConfig.echart_config.chart_x_axis.show_tick">
                    </el-switch>
                  </li>
                  <li>
                    <span class="chart-config-left-label chart-config-item-span">网格线</span>
                    <el-switch class="chart-config-title-switch"
                      v-model="currChartConfig.echart_config.chart_x_axis.show_splitLine">
                    </el-switch>
                  </li>
                </ul>
              </div>
            </li>
          </ul>
          <!-- Y轴配置 -->
          <ul class="chart-cofing-list-li">
            <li class="chart-config-item">
              <div class="chart-config-title" @click="showAllChartYAxisConfig = !showAllChartYAxisConfig">
                <i class="el-iconfont"
                  v-bind:class="{true: 'el-icon-caret-bottom', false: 'el-icon-caret-top'}[showAllChartYAxisConfig]"></i>
                <span>Y轴配置</span>
              </div>
              <div class="chart-config-content chart-config-item-content" v-show="showAllChartYAxisConfig">
                <ul>
                  <li>
                    <span class="chart-config-left-label chart-config-item-span">标题</span>
                    <el-switch class="chart-config-title-switch"
                      v-model="currChartConfig.echart_config.chart_y_axis.show_title">
                    </el-switch>
                  </li>
                  <li v-show="currChartConfig.echart_config.chart_y_axis.show_title">
                    <span class="chart-config-left-label chart-config-item-span">标题内容</span>
                    <el-input class="chart-config-item-input float-right"
                      v-model="currChartConfig.echart_config.chart_y_axis.name">
                    </el-input>
                  </li>
                  <li v-show="currChartConfig.echart_config.chart_y_axis.show_title">
                    <span class="chart-config-left-label chart-config-item-span">标题倾斜角度</span>
                    <el-select class="chart-config-item-input float-right"
                      v-model="currChartConfig.echart_config.chart_y_axis.rotate" placeholder="请选择活动区域">
                      <el-option label="默认" value="0"></el-option>
                      <el-option label="横向" value="0.1"></el-option>
                      <el-option label="竖向" value="90"></el-option>
                      <el-option label="左倾斜45度" value="315"></el-option>
                      <el-option label="右倾斜45度" value="45"></el-option>
                    </el-select>
                  </li>
                  <li>
                    <span class="chart-config-left-label chart-config-item-span">轴线</span>
                    <el-switch class="chart-config-title-switch"
                      v-model="currChartConfig.echart_config.chart_y_axis.show_line">
                    </el-switch>
                  </li>
                  <li>
                    <span class="chart-config-left-label chart-config-item-span">轴标签</span>
                    <el-switch class="chart-config-title-switch"
                      v-model="currChartConfig.echart_config.chart_y_axis.show_label">
                    </el-switch>
                  </li>
                  <li>
                    <span class="chart-config-left-label chart-config-item-span">刻度</span>
                    <el-switch class="chart-config-title-switch"
                      v-model="currChartConfig.echart_config.chart_y_axis.show_tick">
                    </el-switch>
                  </li>
                  <li>
                    <span class="chart-config-left-label chart-config-item-span">网格线</span>
                    <el-switch class="chart-config-title-switch"
                      v-model="currChartConfig.echart_config.chart_y_axis.show_splitLine">
                    </el-switch>
                  </li>
                </ul>
              </div>
            </li>
          </ul>
          <!-- 次Y轴配置 -->
          <ul class="chart-cofing-list-li">
            <li class="chart-config-item">
              <div class="chart-config-title" @click="showAllChartSecondYAxisConfig = !showAllChartSecondYAxisConfig">
                <i class="el-iconfont"
                  v-bind:class="{true: 'el-icon-caret-bottom', false: 'el-icon-caret-top'}[showAllChartSecondYAxisConfig]"></i>
                <span>次Y轴配置</span>
              </div>
              <div class="chart-config-content chart-config-item-content" v-show="showAllChartSecondYAxisConfig">
                <ul>
                  <li>
                    <span class="chart-config-left-label chart-config-item-span">标题</span>
                    <el-switch class="chart-config-title-switch"
                      v-model="currChartConfig.echart_config.chart_y_secondary_axis.show_title">
                    </el-switch>
                  </li>
                  <li v-show="currChartConfig.echart_config.chart_y_secondary_axis.show_title">
                    <span class="chart-config-left-label chart-config-item-span">标题内容</span>
                    <el-input class="chart-config-item-input float-right"
                      v-model="currChartConfig.echart_config.chart_y_secondary_axis.name">
                    </el-input>
                  </li>
                  <li v-show="currChartConfig.echart_config.chart_y_secondary_axis.show_title">
                    <span class="chart-config-left-label chart-config-item-span">标题倾斜角度</span>
                    <el-select class="chart-config-item-input float-right"
                      v-model="currChartConfig.echart_config.chart_y_secondary_axis.rotate" placeholder="请选择活动区域">
                      <el-option label="默认" value="0"></el-option>
                      <el-option label="横向" value="0.1"></el-option>
                      <el-option label="竖向" value="90"></el-option>
                      <el-option label="左倾斜45度" value="315"></el-option>
                      <el-option label="右倾斜45度" value="45"></el-option>
                    </el-select>
                  </li>
                  <li>
                    <span class="chart-config-left-label chart-config-item-span">轴线</span>
                    <el-switch class="chart-config-title-switch"
                      v-model="currChartConfig.echart_config.chart_y_secondary_axis.show_line">
                    </el-switch>
                  </li>
                  <li>
                    <span class="chart-config-left-label chart-config-item-span">轴标签</span>
                    <el-switch class="chart-config-title-switch"
                      v-model="currChartConfig.echart_config.chart_y_secondary_axis.show_label">
                    </el-switch>
                  </li>
                  <li>
                    <span class="chart-config-left-label chart-config-item-span">刻度</span>
                    <el-switch class="chart-config-title-switch"
                      v-model="currChartConfig.echart_config.chart_y_secondary_axis.show_tick">
                    </el-switch>
                  </li>
                </ul>
              </div>
            </li>
          </ul>
          <!-- 数值格式配置 -->
          <ul class="chart-cofing-list-li">
            <li class="chart-config-item">
              <div class="chart-config-title"
                @click="showAllChartDataConversionConfig = !showAllChartDataConversionConfig">
                <i class="el-iconfont"
                  v-bind:class="{true: 'el-icon-caret-bottom', false: 'el-icon-caret-top'}[showAllChartDataConversionConfig]"></i>
                <span>数值格式配置</span>
              </div>
              <div class="chart-config-content chart-config-item-content" v-show="showAllChartDataConversionConfig">
                <ul>
                  <li>
                    <span class="chart-config-left-label chart-config-item-span">数值格式转换</span>
                    <el-switch class="chart-config-title-switch"
                      v-model="currChartConfig.echart_config.chart_data_conversion.show">
                    </el-switch>
                  </li>
                  <li v-show="currChartConfig.echart_config.chart_data_conversion.show">
                    <span class="chart-config-left-label chart-config-item-span">精确位</span>
                    <el-select class="chart-config-item-input float-right"
                      v-model="currChartConfig.echart_config.chart_data_conversion.accurate" placeholder="请选择活动区域">
                      <el-option label="千分位" value="k"></el-option>
                      <el-option label="万分位" value="w"></el-option>
                      <el-option label="亿分位" value="e"></el-option>
                    </el-select>
                  </li>
                  <li v-show="currChartConfig.echart_config.chart_data_conversion.show">
                    <span class="chart-config-left-label chart-config-item-span">保留位</span>
                    <el-select class="chart-config-item-input float-right"
                      v-model="currChartConfig.echart_config.chart_data_conversion.retain" placeholder="请选择活动区域">
                      <el-option label="0" value="0"></el-option>
                      <el-option label="1" value="1"></el-option>
                      <el-option label="2" value="2"></el-option>
                      <el-option label="3" value="3"></el-option>
                    </el-select>
                  </li>
                  <li>
                    <span class="chart-config-left-label chart-config-item-span">开启百分比转换</span>
                    <el-switch class="chart-config-title-switch"
                      v-model="currChartConfig.echart_config.chart_data_conversion.percentage">
                    </el-switch>
                  </li>
                  <li>
                    <span class="chart-config-left-label chart-config-item-span">开启仪表盘范围设置</span>
                    <el-switch class="chart-config-title-switch"
                      v-model="currChartConfig.echart_config.chart_data_conversion.range">
                    </el-switch>
                  </li>
                  <li v-show="currChartConfig.echart_config.chart_data_conversion.range">
                    <span class="chart-config-left-label chart-config-item-span">最大值</span>
                    <el-input type="number" class="chart-config-item-input float-right"
                      v-model="currChartConfig.echart_config.chart_data_conversion.max">
                    </el-input>
                  </li>
                  <li v-show="currChartConfig.echart_config.chart_data_conversion.range">
                    <span class="chart-config-left-label chart-config-item-span">最小值</span>
                    <el-input type="number" class="chart-config-item-input float-right"
                      v-model="currChartConfig.echart_config.chart_data_conversion.min">
                    </el-input>
                  </li>
                  <li v-show="currChartConfig.echart_config.chart_data_conversion.range">
                    <span class="chart-config-left-label chart-config-item-span">仪表盘标题</span>
                    <el-input class="chart-config-item-input float-right"
                      v-model="currChartConfig.echart_config.chart_data_conversion.name">
                    </el-input>
                  </li>
                </ul>
              </div>
            </li>
          </ul>
          <!-- 功能配置 -->
          <ul class="chart-cofing-list-li">
            <li class="chart-config-item">
              <div class="chart-config-title" @click="showAllChartFunctionConfig = !showAllChartFunctionConfig">
                <i class="el-iconfont"
                  v-bind:class="{true: 'el-icon-caret-bottom', false: 'el-icon-caret-top'}[showAllChartFunctionConfig]"></i>
                <span>功能配置</span>
              </div>
              <div class="chart-config-content chart-config-item-content" v-show="showAllChartFunctionConfig">
                <ul>
                  <li>
                    <span class="chart-config-left-label chart-config-item-span">预警线</span>
                    <i class="chart-config-right-label el-icon-edit chart-config-item-edit"></i>
                  </li>
                  <li>
                    <span class="chart-config-left-label chart-config-item-span">辅助线</span>
                    <i class="chart-config-right-label el-icon-edit chart-config-item-edit"></i>
                  </li>
                  <li>
                    <span class="chart-config-left-label chart-config-item-span">显示条目数</span>
                    <el-switch class="chart-config-title-switch" v-model="currChartConfig.limit.show">
                    </el-switch>
                  </li>
                  <li v-show="currChartConfig.limit.show">
                    <el-select class="chart-config-limit-type" v-model="currChartConfig.limit.type"
                      placeholder="请选择活动区域">
                      <el-option label="前" value="before"></el-option>
                      <el-option label="后" value="after"></el-option>
                    </el-select>
                    <el-input class="chart-config-limit-num" type="number" v-model="currChartConfig.limit.num">
                    </el-input>
                    <span class="chart-config-right-label">条</span>
                  </li>
                </ul>
              </div>
            </li>
          </ul>
          <!-- 分析交互配置 -->
          <ul class="chart-cofing-list-li">
            <li class="chart-config-item">
              <div class="chart-config-title" @click="showAllChartInteractiveConfig = !showAllChartInteractiveConfig">
                <i class="el-iconfont"
                  v-bind:class="{true: 'el-icon-caret-bottom', false: 'el-icon-caret-top'}[showAllChartInteractiveConfig]"></i>
                <span>分析交互配置</span>
              </div>
              <div class="chart-config-content chart-config-item-content" v-show="showAllChartInteractiveConfig">
                <ul>
                  <li>
                    <span class="chart-config-left-label">显示缩略轴</span>
                    <el-switch class="chart-config-title-switch"
                      v-model="currChartConfig.echart_config.chart_zoom.show">
                    </el-switch>
                  </li>
                </ul>
              </div>
            </li>
          </ul>
        </el-tab-pane>
      </el-tabs>
    </div>
    <div v-show="currChartConfig.select_type !=='C200'" class="export-charts-png"
      @click="saveEchartsPng(currChartConfig.select_type)">
      <span>完成配置，生成图表</span>
      <i class="el-icon-right"></i>
    </div>
    <div v-show="currChartConfig.select_type ==='C200'" class="export-charts-png" @click="exportExcel">
      <span>完成配置，生成 EXCEL 文件</span>
      <i class="el-icon-right"></i>
    </div>
  </div>
</template>
<script>
  import {
    debounce
  } from 'lodash'

  export default {
    name: 'ChartConfig',
    data() {
      return {

        chartsConfigTabActive: "common-config",

        currChartConfig: {},

        showChartsConfigPanel: true,

        showCommonChartTypeConfig: true,

        showCommonChartLegendConfig: true,

        showCommonChartLabelConfig: true,

        showCommonChartFunctionConfig: true,


        showAllChartTypeConfig: true,

        showAllChartThemeConfig: true,

        showAllChartTextConfig: true,

        showAllChartLegendConfig: true,

        showAllChartLabelConfig: true,

        showAllChartDataConversionConfig: true,

        showAllChartFunctionConfig: true,

        showAllChartInteractiveConfig: true,

        showAllChartTipsConfig: true,

        showAllChartXAxisConfig: true,

        showAllChartYAxisConfig: true,

        showAllChartSecondYAxisConfig: true,


      }
    },
    //(创建后)
    created() {

      this.currChartConfig = this.$store.state.defaultChartConfig;

    },
    //钩子(载入后)
    mounted() {

    },

    //函数列表
    methods: {

      /* function - selectedChartType
       * 图表类型切换
       * @param {Object} itemConfig - 图表类型配置项
       * @param {String} refresh - 刷新图表标志
       */

      selectedChartType(itemConfig) {

        if (itemConfig.type === this.currChartConfig.select_type) {

          return false;

        } else if (itemConfig.enable === false) {

          this.$message({
            message: '图表类型禁用，请根据图表类型配置数据列',
            type: 'warning'
          });

          return false;

        } else {

          this.currChartConfig.select_type = JSON.parse(JSON.stringify(itemConfig.type));

        }

      },

      exportExcel() {

        /* 从表生成工作簿对象 */
        var wb = this.$XLSX.utils.table_to_book(document.querySelector('#data-table'))
        /* 获取二进制字符串作为输出 */
        var wbout = this.$XLSX.write(wb, {
          bookType: 'xlsx',
          bookSST: true,
          type: 'array'
        })
        try {
          // Blob 对象表示一个不可变、原始数据的类文件对象。
          // Blob 表示的不一定是JavaScript原生格式的数据。
          // File 接口基于Blob，继承了 blob 的功能并将其扩展使其支持用户系统上的文件。
          // 返回一个新创建的 Blob 对象，其内容由参数中给定的数组串联组成。
          this.$FileSaver.saveAs(
            new Blob([wbout], {
              type: 'application/octet-stream'
            }),
            // 设置导出文件名称
            '数据报表文件.xlsx')
        } catch (e) {
          if (typeof console !== 'undefined') console.log(e, wbout)
        }
        return wbout
      },
      dataURLToBlob(dataurl) {

        let arr = dataurl.split(',');

        let mime = arr[0].match(/:(.*?);/)[1];

        let bstr = atob(arr[1]);

        let n = bstr.length;

        let u8arr = new Uint8Array(n);

        while (n--) {

          u8arr[n] = bstr.charCodeAt(n);

        }

        return new Blob([u8arr], {
          type: mime
        });

      },
      //保存并生成下载图表图片
      saveEchartsPng(imgText) {

        let canvasID = document.getElementById('export-chart-box');

        let that = this;

        let a = document.createElement('a');

        this.$html2canvas(canvasID).then(canvas => {

          let dom = document.body.appendChild(canvas);

          dom.style.display = 'none';

          a.style.display = 'none';

          document.body.removeChild(dom);

          let blob = that.dataURLToBlob(dom.toDataURL('image/png'));

          a.setAttribute('href', URL.createObjectURL(blob));

          //这块是保存图片操作  可以设置保存的图片的信息
          a.setAttribute('download', imgText + '.png');

          document.body.appendChild(a);

          a.click();

          URL.revokeObjectURL(blob);

          document.body.removeChild(a);

        });

        this.$message({
          message: '图表配置导出成功！',
          type: 'success'
        });

      },

    },

    computed: {



    },
    watch: {
      "$store.state.defaultChartConfig": {
        handler(newChartConfig, oldnewChartConfig) {

          var selectedFieldList = JSON.parse(JSON.stringify(this.$store.state.defaultChartConfig.analysis_column));

          if (selectedFieldList.data_2.length === 0 &&
            selectedFieldList.data_4.length === 0 &&
            selectedFieldList.data_4.length === 0) {

            return false;

          } else {

            setTimeout(() => {

              this.$store.commit('renderChartService', this.$echarts);

            }, 500);

          }

        },
        deep: true //true 深度监听
      }
    }
  }
</script>
<style scoped>
  .left-sidebar {
    position: absolute;
    top: 0;
    width: 200px;
    bottom: 0;
    left: 0;
    padding: 15px 15px 10px;
    background: #fff;
    border-right: #e8e8e8 1px solid;
    z-index: 99;
  }

  .chart-config-item-input {
    width: 130px;
  }

  .chart-config-limit-type {
    width: 70px;
  }

  .chart-config-limit-num {
    width: 130px;
  }

  .el-tabs__nav-wrap.is-scrollable {
    padding: 0 10px;
  }

  .chart-config-title-switch {
    float: right;
    margin: 5px 0 0;
  }

  .chart-config-title span {
    text-indent: 5px;
    display: inline-block;
    color: #555;
  }

  .el-tabs--border-card {
    background: #FFF;
    border: none;
    -webkit-box-shadow: none;
    box-shadow: none;
  }

  .chart-config-item-content>ul>li {
    display: inline-block;
    position: relative;
    height: 30px;
    margin: 15px 0 0 15px;
    width: calc(100% - 25px);
  }

  .chart-uses-tips {
    position: fixed;
    width: 210px;
    padding: 12px;
    top: 120px;
    right: 262px;
    height: auto;
    background-color: #FFF;
    -webkit-box-shadow: 0 2px 4px 0 rgb(0 0 0 / 10%), 0 16px 24px 0 rgb(81 129 228 / 10%);
    box-shadow: 0 2px 4px 0 rgb(0 0 0 / 10%), 0 16px 24px 0 rgb(81 129 228 / 10%);
    z-index: 999;
    border-radius: 4px;
    color: #555;
    border: 1px solid #e6e6e6;
    text-align: left;
    display: none;
    line-height: 25px;
  }

  .chart-config-left-label {
    float: left;
    height: 30px;
    line-height: 30px;
  }

  .chart-config-right-label {
    float: right;
    height: 30px;
    line-height: 30px;

  }

  .chart-config-title .el-iconfont {
    font-size: 18px;
    color: #666;
    margin: 5px 0 0;
    float: left;
  }

  .chart-config-title {
    cursor: pointer;
    height: 30px;
    line-height: 30px;
    margin: 0;
    font-weight: 700;
    font-size: 12px;
    padding: 7px 15px 6px;
    letter-spacing: 0.5px;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
  }

  .chart-item-type:hover .chart-uses-tips {

    display: block;

  }

  .chart-type-bar ul li {
    display: inline-block;
    text-align: center;
    float: left;
    position: relative;
    height: 39px;
    margin: 10px 8px 0px 10px;
  }


  .chart-type-bar ul li a {
    height: 35px;
    line-height: 30px;
    padding: 2px 5px;
    margin: 0;
    display: block;
    width: 30px;
    cursor: pointer;
  }

  .chart-config-item-edit {
    font-size: 18px;
    color: #666;
    cursor: pointer;
  }

  .chart-cofing-list {
    position: absolute;
    top: 0;
    width: 260px;
    bottom: 45px;
    left: 0;
    padding: 0;
    background: #fff;
    border-right: #e8e8e8 1px solid;
    z-index: 99;
    overflow: auto;
  }

  .chart-config-content {
    padding: 0px 0 25px 0;
  }

  .chart-config-item-span {
    font-weight: 700;
    color: #777;
    font-size: 12px;
  }

  .export-charts-png {
    width: 100%;
    height: 45px;
    line-height: 45px;
    position: absolute;
    bottom: 0;
    right: 0;
    text-align: center;
    z-index: 10;
    border-radius: 0;
    color: #fff;
    background-color: #984fff;
    border-color: #984fff;
    cursor: pointer;
    letter-spacing: 1px;
  }

  .chart-config-theme-input {
    width: 100%;
  }

  .color-list-select {
    margin: 5px 0 0 15px;
  }

  .chart-config-theme-list .el-dropdown-link {
    cursor: pointer;
  }

  .color-select-icon {
    margin: 3px 0 0 3px;
  }
</style>



