<template>
  <div>
    <!--分析栏视图-->
    <div class="analysis-bar">
      <ul>
        <!--维度栏视图-->
        <li class="dimension-data-list">
          <div class="analysis-data-item-title">
            <span>X轴</span>
            <i class="iconfont icon-edit" @click="addArgsData('dimension')"></i>
          </div>
          <!--已选字段列表-->
          <ul class="selected-field-tags">
            <li class="analysis-data-item"
              v-for="(dimensionItem,index) in $store.state.defaultChartConfig.analysis_column.data_2" :key="index">
              <span>{{dimensionItem.newName}}</span>
              <i class="iconfont icon-item-delete" @click="deleteArgsData('dimension',index)"></i>
            </li>
          </ul>
        </li>
        <!--数值一栏视图-->
        <li>
          <div class="analysis-data-item-title">
            <span>Y轴</span>
            <i class="iconfont icon-edit" @click="addArgsData('valueOne')"></i>
          </div>
          <!--双轴图选项-->
          <el-dropdown v-show="$store.state.defaultChartConfig.select_type==='C250'">
            <span class="el-dropdown-link">
              <svg class="multicolor-icon"
                v-show="$store.state.defaultChartConfig.echart_config.chart_y_secondary_axis.chart_type_one === 'C220'">
                <use xlink:href="#icon-fsux_zhexiantu"></use>
              </svg>
              <svg class="multicolor-icon"
                v-show="$store.state.defaultChartConfig.echart_config.chart_y_secondary_axis.chart_type_one === 'C210'">
                <use xlink:href="#icon-fsux_tubiao_zhuzhuangtu1"></use>
              </svg>
              <svg class="multicolor-icon"
                v-show="$store.state.defaultChartConfig.echart_config.chart_y_secondary_axis.chart_type_one === 'C211'">
                <use xlink:href="#icon-fsux_tubiao_duijizhuzhuangtu"></use>
              </svg>
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu class="select-chart-more-type" slot="dropdown">
              <el-dropdown-item>
                <a @click="switchDualAxisChartType('one','C220')">
                  <svg class="multicolor-icon">
                    <use xlink:href="#icon-fsux_zhexiantu"></use>
                  </svg>
                </a>
              </el-dropdown-item>
              <el-dropdown-item>
                <a @click="switchDualAxisChartType('one','C210')">
                  <svg class="multicolor-icon">
                    <use xlink:href="#icon-fsux_tubiao_zhuzhuangtu1"></use>
                  </svg>
                </a>
              </el-dropdown-item>
              <el-dropdown-item>
                <a @click="switchDualAxisChartType('one','C211')">
                  <svg class="multicolor-icon">
                    <use xlink:href="#icon-fsux_tubiao_duijizhuzhuangtu"></use>
                  </svg>
                </a>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <!--已选字段列表-->
          <ul class="selected-field-tags">
            <li class="analysis-data-item"
              v-for="(valueOneItem,index) in $store.state.defaultChartConfig.analysis_column.data_4" :key="index">
              <span>{{valueOneItem.newName}}</span>
              <i class="iconfont icon-item-delete" @click="deleteArgsData('valueOne',index)"></i>
            </li>
          </ul>
        </li>
        <!--数值二栏视图-->
        <li>
          <div class="analysis-data-item-title">
            <span>次Y轴</span>
            <i class="iconfont icon-edit" @click="addArgsData('valueTwo')"></i>
          </div>
          <!--双轴图选项-->
          <el-dropdown v-show="$store.state.defaultChartConfig.select_type==='C250'">
            <span class="el-dropdown-link">
              <svg class="multicolor-icon"
                v-show="$store.state.defaultChartConfig.echart_config.chart_y_secondary_axis.chart_type_two === 'C220'">
                <use xlink:href="#icon-fsux_zhexiantu"></use>
              </svg>
              <svg class="multicolor-icon"
                v-show="$store.state.defaultChartConfig.echart_config.chart_y_secondary_axis.chart_type_two === 'C210'">
                <use xlink:href="#icon-fsux_tubiao_zhuzhuangtu1"></use>
              </svg>
              <svg class="multicolor-icon"
                v-show="$store.state.defaultChartConfig.echart_config.chart_y_secondary_axis.chart_type_two === 'C211'">
                <use xlink:href="#icon-fsux_tubiao_duijizhuzhuangtu"></use>
              </svg>
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu class="select-chart-more-type" slot="dropdown">
              <el-dropdown-item>
                <a @click="switchDualAxisChartType('two','C220')">
                  <svg class="multicolor-icon">
                    <use xlink:href="#icon-fsux_zhexiantu"></use>
                  </svg>
                </a>
              </el-dropdown-item>
              <el-dropdown-item>
                <a @click="switchDualAxisChartType('two','C210')">
                  <svg class="multicolor-icon">
                    <use xlink:href="#icon-fsux_tubiao_zhuzhuangtu1"></use>
                  </svg>
                </a>
              </el-dropdown-item>
              <el-dropdown-item>
                <a @click="switchDualAxisChartType('two','C211')">
                  <svg class="multicolor-icon">
                    <use xlink:href="#icon-fsux_tubiao_duijizhuzhuangtu"></use>
                  </svg>
                </a>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <!--已选字段列表-->
          <ul class="selected-field-tags">
            <li class="analysis-data-item"
              v-for="(valueTwoItem,index) in $store.state.defaultChartConfig.analysis_column.data_5" :key="index">
              <span>{{valueTwoItem.newName}}</span>
              <i class="iconfont icon-item-delete" @click="deleteArgsData('valueTwo',index)"></i>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <!--对话框 - 添加字段操作列-->
    <el-dialog :title="addArgsDataTitle" :visible.sync="addArgsDataItemDialog" width="800px" top="50px">
      <el-row>
        <el-col :span="12" class="add-args-list">
          <div class="grid-content bg-purple">
            <el-input v-model="canSelectFieldsInput" placeholder="请输入需要搜索的字段"
              @input="canSelectfieldListFilterDebounce('canSelect')"></el-input>
            <ul class="can-fields-list">
              <li v-for="(fieldItem,index) in canSelectFieldsList" :key="index">
                <div class="original-fields-item" @dblclick="addFieldItem(fieldItem)">
                  <i class="iconfont icon-authority-cms"></i>
                  <span class="original-fields-item-name nowrap"
                    v-bind:title="fieldItem.newName">{{fieldItem.newName}}</span>
                  <i class="add-arg-field-icon el-icon-plus" title="添加字段" @click="addFieldItem(fieldItem)"></i>
                </div>
              </li>
            </ul>
          </div>
        </el-col>
        <el-col :span="12" class="add-args-list">
          <div class="grid-content bg-purple-light">
            <div class="grid-content bg-purple">
              <el-input v-model="selectFieldsInput" placeholder="请输入需要搜索的字段"
                @input="canSelectfieldListFilterDebounce('selected')"></el-input>
              <ul class="can-fields-list">
                <li v-for="(fieldItem,index) in selectedfieldListInputFilter" :key="index">
                  <div class="original-fields-item" @dblclick="delFieldItem(fieldItem,index)">
                    <i class="iconfont icon-authority-cms"></i>
                    <span class="original-fields-item-name nowrap"
                      v-bind:title="fieldItem.newName">{{fieldItem.newName}}</span>
                    <i class="add-arg-field-icon el-icon-delete" title="删除字段"
                      @click="delFieldItem(fieldItem,index)"></i>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </el-col>
      </el-row>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addArgsDataItemDialog = false">取 消</el-button>
        <el-button type="primary" @click="addArgsDataItemSubmit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    name: 'columnSelected',
    components: {},
    data() {
      return {
        chartTypeOneShow: false,
        chartTypeTwoShow: false,

        canSelectFieldsInput: "",

        addArgsDataType: "",

        selectFieldsInput: "",

        addArgsDataTitle: "",
        addArgsDataItemDialog: false,
        addArgsDataForm: {},

        canSelectFieldsList: [],
        selectedFieldsList: [],
        addArgsDataValue: [],


      }
    },

    //函数列表
    methods: {

      //已选的数据列数据项目添加
      addArgsData(type) {

        this.addArgsDataType = type;

        this.addArgsDataItemDialog = true;

        if (type === "dimension") {

          this.addArgsDataTitle = "批量添加 X 轴字段";

          //公共已选数据列(X轴)方法共享
          this.selectedFieldsList = JSON.parse(JSON.stringify(this.$store.state.defaultChartConfig.analysis_column
            .data_2));

        } else if (type === "valueOne") {

          this.addArgsDataTitle = "批量添加 Y 轴字段";

          //公共已选数据列(Y轴)方法共享
          this.selectedFieldsList = JSON.parse(JSON.stringify(this.$store.state.defaultChartConfig.analysis_column
            .data_4));

        } else if (type === "valueTwo") {

          this.addArgsDataTitle = "批量添加次 Y 轴字段";

          //公共已选数据列(次Y轴)方法共享
          this.selectedFieldsList = JSON.parse(JSON.stringify(this.$store.state.defaultChartConfig.analysis_column
            .data_5));

        }

        //公共可操作数据列方法共享
        this.canSelectFieldsList = JSON.parse(JSON.stringify(this.$store.state.fieldList));

      },

      //删除已选的数据列数据项目添加
      deleteArgsData(type, index) {

        if (type === "dimension") {

          this.$store.state.defaultChartConfig.analysis_column.data_2.splice(index, 1);

        } else if (type === "valueOne") {

          this.$store.state.defaultChartConfig.analysis_column.data_4.splice(index, 1);

        } else if (type === "valueTwo") {

          this.$store.state.defaultChartConfig.analysis_column.data_5.splice(index, 1);

        }

      },
      //添加单项数据字段
      addFieldItem(item) {

        var item = JSON.parse(JSON.stringify(item));

        this.selectedFieldsList.push(item);

      },
      //删除单项数据字段
      delFieldItem(item, index) {

        this.selectedFieldsList.splice(index, 1);

      },

      //保存已选字段方法
      addArgsDataItemSubmit() {

        var selectedFieldsList = JSON.parse(JSON.stringify(this.selectedFieldsList));

        if (this.addArgsDataType === "dimension") {

          this.$store.state.defaultChartConfig.analysis_column.data_2 = selectedFieldsList;

        } else if (this.addArgsDataType === "valueOne") {

          this.$store.state.defaultChartConfig.analysis_column.data_4 = selectedFieldsList;

        } else if (this.addArgsDataType === "valueTwo") {

          this.$store.state.defaultChartConfig.analysis_column.data_5 = selectedFieldsList;

        }

        this.addArgsDataItemDialog = false;

      },

      //可操作字段列表过滤器
      canSelectfieldListFilter: _.debounce(function(type) {

        if (type === 'canSelect') {

          var searchFieldsInput = this.canSelectFieldsInput;

          var allFieldList = JSON.parse(JSON.stringify(this.$store.state.fieldList));

        } else {

          var searchFieldsInput = this.selectFieldsInput;

          var allFieldList = JSON.parse(JSON.stringify(this.selectedFieldsList));

        }

        var matchItemList = [];

        allFieldList.filter(function(item) {

          item = JSON.parse(JSON.stringify(item));

          if (item.newName.indexOf(searchFieldsInput) !== -1) {

            matchItemList.push(item);

          }

        })

        if (type === 'canSelect') {

          this.canSelectFieldsList = matchItemList;

        } else {

          if (searchFieldsInput === '') {

            if (this.addArgsDataType === "dimension") {

              this.selectedFieldsList = JSON.parse(JSON.stringify(this.$store.state.defaultChartConfig.analysis_column.data_2));

            } else if (this.addArgsDataType === "valueOne") {

              this.selectedFieldsList = JSON.parse(JSON.stringify(this.$store.state.defaultChartConfig.analysis_column.data_4));

            } else if (this.addArgsDataType === "valueTwo") {

              this.selectedFieldsList = JSON.parse(JSON.stringify(this.$store.state.defaultChartConfig.analysis_column.data_5));

            }

          } else {

            this.selectedFieldsList = matchItemList;

          }

        }

      }, 800),

      canSelectfieldListFilterDebounce(type) {

        this.canSelectfieldListFilter(type);

      },

      //双轴图切换方法
      switchDualAxisChartType(type, chartsType) {

        var chartsType = JSON.parse(JSON.stringify(chartsType));

        if (type === "one") {

          this.$store.state.defaultChartConfig.echart_config.chart_y_secondary_axis.chart_type_one = chartsType;

        } else {

          this.$store.state.defaultChartConfig.echart_config.chart_y_secondary_axis.chart_type_two = chartsType;

        }

      },

      /* function - chartTypeCondition
       * 根据已选字段列表判断图表类型的可用性
       */
      chartTypeCondition: function(chartConfig, chartTypeList) {

        var newChartTypeList = [];

        for (var idx in chartTypeList) {

          var itemChartType = chartTypeList[idx];

          itemChartType.enable = true;

          for (var i = 0; i < 6; i++) {

            var argsItem = chartConfig['data_' + i];

            var needCondition = itemChartType.needCondition[i];

            if (argsItem.length < needCondition) {

              itemChartType.enable = false;
            }

          }

          newChartTypeList.push(itemChartType);

        }

        return newChartTypeList;
      },
    },
    computed: {

      //可操作字段列表过滤器
      canSelectfieldListInputFilter: function(value) {

        var allFieldList = JSON.parse(JSON.stringify(this.canSelectFieldsList))

        var matchItemList = [];

        if (!this.canSelectFieldsInput) {

          return allFieldList;

        } else {

          for (var idx in allFieldList) {

            var modelItem = allFieldList[idx];

            if (modelItem && modelItem.newName.indexOf(this.canSelectFieldsInput) > -1) {

              matchItemList.push(modelItem);
            }
          }
        }

        return matchItemList;

      },
      //已选操作字段列表过滤器
      selectedfieldListInputFilter: function(value) {

        var allFieldList = JSON.parse(JSON.stringify(this.selectedFieldsList))

        var matchItemList = [];

        if (!this.selectFieldsInput) {

          return allFieldList;

        } else {

          for (var idx in allFieldList) {

            var modelItem = allFieldList[idx];

            if (modelItem && modelItem.newName.indexOf(this.selectFieldsInput) > -1) {

              matchItemList.push(modelItem);
            }
          }
        }

        return matchItemList;

      }
    },
    watch: {
      "$store.state.defaultChartConfig.analysis_column": {
        handler(newChartConfig, oldChartConfig) {

          var selectedFieldList = JSON.parse(JSON.stringify(this.$store.state.defaultChartConfig.analysis_column));

          var chartTypeList = JSON.parse(JSON.stringify(this.$store.state.chartTypeList));

          var newChartTypeList = this.chartTypeCondition(selectedFieldList, chartTypeList);

          this.$store.state.chartTypeList = newChartTypeList;

        },
        deep: true //true 深度监听
      }
    }
  }
</script>
<style scoped>
  .can-fields-list li:hover .add-arg-field-icon {
    opacity: 1;
  }

  .add-arg-field-icon {
    float: right;
    height: 35px;
    line-height: 35px;
    font-size: 16px;
    color: #888;
    padding: 0 5px;
    opacity: 0;
  }

  .left-sidebar {
    position: absolute;
    top: 0;
    width: 200px;
    bottom: 0;
    left: 0;
    padding: 15px 15px 10px;
    background: #fff;
    border-right: #e8e8e8 1px solid;
    z-index: 99;
  }

  .el-dropdown {
    display: inline-block;
    width: 55px;
    float: left;
    height: 38px;
    padding: 0;
    margin: 0 8px 0 70px;
    position: relative;
  }

  .el-dropdown-link {
    cursor: pointer;
    color: #409EFF;
    height: 32px;
    display: inline-block;
    width: 55px;
    line-height: 32px;
    border-radius: 4px;
  }

  .el-dropdown-link i {
    float: right;
    margin: 10px 5px 0 0;
  }

  .el-dropdown-link:hover {
    background: #f0f0f0;
  }

  .el-dropdown-menu .multicolor-icon {
    margin: 3px 0 0;
  }

  .selected-field-tags>div,
  .selected-field-tags>div>span {
    display: block;
    height: 32px;
    width: 100%;
  }

  .el-dropdown-link>.multicolor-icon {
    width: 1em;
    height: 1em;
    fill: currentColor;
    overflow: hidden;
    font-size: 25px;
    margin: 4px 0 0 7px;
    float: left;
  }

  .el-icon-arrow-down {
    font-size: 12px;
  }

  .dimension-data-list>ul {
    padding: 0 0 0 70px;
  }

  .el-iconfont {
    font-size: 12px;
    color: #888;
    font-weight: 700;
  }

  .can-fields-list {
    height: 265px;
    overflow: auto;
    padding: 5px 0;
  }


  .can-fields-list li {
    line-height: 35px;
    height: 35px;
    margin: 0;
    padding: 0 5px;
    cursor: pointer;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    position: relative;
  }

  .can-fields-list li:hover {
    background: #eee;
  }

  .add-args-list {
    padding: 0 15px 0 10px;
  }

  .select-chart-more-type li {
    height: 32px;
    display: block;
    padding: 0;
  }

  .select-chart-more-type li a {
    height: 32px;
    display: inline-block;
    padding: 0 15px;
  }
</style>



