<template>
  <div class="modal-body general-chart-design">
    <!-- 顶部导航区域 -->
    <headerNavi />
    <!-- 图表主设计区域 -->
    <div class="main-area">
      <!-- 可操作的数据列区域 -->
      <transition name="fade">
        <columnList v-show="switchChartsPanelConfig.showChartsListPanel" />
      </transition>
      <!-- 已选的数据列区域 -->
      <div class="center-sidebar" v-bind:class="switchChartsPanelConfig.switchChartsPanelClass">
        <a class="switch-display-config switch-display-chart-list" @click="switchChartsListPanel('left')">
          <i v-bind:class="switchChartsPanelConfig.showChartsListPanelClassIcon"></i>
        </a>
        <columnSelected />
        <!-- 图表渲染容器区域 -->
        <chartsRender />
        <a class="switch-display-config switch-display-chart-config" @click="switchChartsListPanel('right')">
          <i v-bind:class="switchChartsPanelConfig.showChartsConfigPanelClassIcon"></i>
        </a>
      </div>
      <!-- 图表配置区域 -->
      <transition name="fade">
        <chartConfig v-show="switchChartsPanelConfig.showChartsConfigPanel" />
      </transition>
    </div>

  </div>
</template>
<script>
  import HeaderNavi from '../common/HeaderNavi'

  import ColumnList from '../content/ColumnList'

  import ColumnSelected from '../content/ColumnSelected'

  import ChartsRender from '../content/ChartsRender'

  import ChartConfig from '../content/ChartConfig'

  export default {
    name: 'HomePage',
    components: {
      HeaderNavi,
      ColumnList,
      ColumnSelected,
      ChartsRender,
      ChartConfig
    },
    data() {
      return {

        switchChartsPanelConfig: {

          switchChartsPanelClass: '',

          showChartsListPanel: true,

          showChartsListPanelClassIcon: 'el-icon-arrow-left',

          showChartsConfigPanel: true,

          showChartsConfigPanelClassIcon: 'el-icon-arrow-right',

        }

      }
    },
    //函数列表
    methods: {

      //两侧区域列表收缩与展开切换
      switchChartsListPanel(type) {

        if (type === 'left') {

          this.switchChartsPanelConfig.showChartsListPanel = !this.switchChartsPanelConfig.showChartsListPanel;

        } else {

          this.switchChartsPanelConfig.showChartsConfigPanel = !this.switchChartsPanelConfig.showChartsConfigPanel;
        }

        //配置收缩样式切换处理
        if (this.switchChartsPanelConfig.showChartsListPanel === false &&
          this.switchChartsPanelConfig.showChartsConfigPanel === false) {

          this.switchChartsPanelConfig.switchChartsPanelClass = 'center-all-sidebar';

          this.switchChartsPanelConfig.showChartsListPanelClassIcon = 'el-icon-arrow-right';

          this.switchChartsPanelConfig.showChartsConfigPanelClassIcon = 'el-icon-arrow-left';

        } else if (this.switchChartsPanelConfig.showChartsListPanel === false &&
          this.switchChartsPanelConfig.showChartsConfigPanel === true) {

          this.switchChartsPanelConfig.switchChartsPanelClass = 'center-left-sidebar';

          this.switchChartsPanelConfig.showChartsListPanelClassIcon = 'el-icon-arrow-right';

          this.switchChartsPanelConfig.showChartsConfigPanelClassIcon = 'el-icon-arrow-right';

        } else if (this.switchChartsPanelConfig.showChartsListPanel === true &&
          this.switchChartsPanelConfig.showChartsConfigPanel === false) {

          this.switchChartsPanelConfig.switchChartsPanelClass = 'center-right-sidebar';

          this.switchChartsPanelConfig.showChartsListPanelClassIcon = 'el-icon-arrow-left';

          this.switchChartsPanelConfig.showChartsConfigPanelClassIcon = 'el-icon-arrow-left';

        } else if (this.switchChartsPanelConfig.showChartsListPanel === true &&
          this.switchChartsPanelConfig.showChartsConfigPanel === true) {

          this.switchChartsPanelConfig.switchChartsPanelClass = '';

          this.switchChartsPanelConfig.showChartsListPanelClassIcon = 'el-icon-arrow-left';

          this.switchChartsPanelConfig.showChartsConfigPanelClassIcon = 'el-icon-arrow-right';

        }

      },
    },
    watch: {
      "switchChartsPanelConfig": {
        handler(newChartConfig, oldnewChartConfig) {

          var selectedFieldList = JSON.parse(JSON.stringify(this.$store.state.defaultChartConfig.analysis_column));

          if (selectedFieldList.data_2.length === 0 &&
            selectedFieldList.data_4.length === 0 &&
            selectedFieldList.data_4.length === 0) {

            return false;

          } else {

            setTimeout(() => {

              this.$store.commit('renderChartService', this.$echarts);

            }, 500);

          }

        },
        deep: true //true 深度监听
      }
    }

  }
</script>
<style>
  .main-area {
    position: absolute;
    height: calc(100% - 49px);
    width: 100%;
    top: 49px;
  }

  .switch-display-config {
    position: absolute;
    top: 50%;
    height: 30px;
    width: 15px;
    line-height: 30px;
    z-index: 800;
    background: #eee;

    cursor: pointer;
    margin: -15px 0 0 0;
  }

  .switch-display-chart-config {
    right: 0;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
  }

  .switch-display-chart-list {
    left: 0;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
  }

  .switch-display-chart-config i {
    color: #aaa;
  }

  .switch-display-chart-config:hover {
    background: #ececec;
  }

  .fade-enter-active,
  .fade-leave-active {
    transition: opacity .5s;
  }

  .fade-enter,
  .fade-leave-to {
    opacity: 0;
  }
</style>



