const EchartsWordCloudParseModule = {

  state: {

  },

  mutations: {
    //监听图表配置函数
    pieParseOptionInit(state,colorList) {

      //数据集
      var dataSetList = JSON.parse(JSON.stringify(state.dataSetList));

      var defaultChartConfig = JSON.parse(JSON.stringify(state.defaultChartConfig));

      //所选图表类型
      var selected_chart_type = defaultChartConfig.select_type;

      //图表标题配置
      var chartText = defaultChartConfig.echart_config.chart_text;
      
      //图表主题
      var mainTheme = defaultChartConfig.chart_theme.main_theme;

      //图表悬浮提示
      var chartTips = defaultChartConfig.echart_config.chart_tips;

      //图表数据转换配置
      var chartDataConversion = defaultChartConfig.echart_config.chart_data_conversion;

      //图表图例配置
      var chartLegend = defaultChartConfig.echart_config.chart_legend;

      //获取已选字段 X轴 第一个字段
      var xAxisItem = defaultChartConfig.analysis_column.data_2[0];

      //获取已选字段 Y轴 第一个字段
      var yAxisItem = defaultChartConfig.analysis_column.data_4[0];

      //图表标签显示配置
      var chartLabelShow = defaultChartConfig.echart_config.chart_label.show;

      //数据显示条目数
      var dataSetListLimit = defaultChartConfig.limit;

      var dataSetLimitNum = parseInt(defaultChartConfig.limit.num);

      if (dataSetListLimit.show === true) {

        if (dataSetList.length >= dataSetLimitNum) {

          if (defaultChartConfig.limit.type === 'before') {

            dataSetList = dataSetList.splice(0, dataSetLimitNum);

          } else {

            dataSetList = dataSetList.splice(dataSetList.length - dataSetLimitNum, dataSetList.length - 1);

          }
        }

      }
      
      if(mainTheme === "#fff"){
      
        var fontColor = "#2c2c3d";
      
      }else{
      
        var fontColor = "#fff";
      
      }

      //图表图例数据列表
      var legendDataOption = {
        icon: 'circle',
        type: 'scroll',
        itemHeight: 10,
        itemWidth: 10,
        show: chartLegend.show,
        left: chartLegend.horizontal,
        top: chartLegend.vertical,
        data: [],
        textStyle:{
          color:fontColor
        }
      };


      //序列数据列表
      var seriesDataListOption = [];

      //序列单项数据列表
      for (var index in dataSetList) {

        // if (chartDataSwitchConfig.show === true) {

        //   dataSetList[index][yAxisItem.name] = dataSwitchFunction(dataSetList[index][yAxisItem.name],
        //     chartDataSwitchConfig);

        // }

        legendDataOption.data.push(dataSetList[index][xAxisItem.newName]);

        seriesDataListOption.push({
          name: dataSetList[index][xAxisItem.name],
          value: dataSetList[index][yAxisItem.name]
        });

      }

      //图表悬浮提示配置
      var tooltipOption = {
        show: chartTips.show,
        backgroundColor: '#fff',
        padding: 10,
        textStyle: {
          color: "#666"
        },
        borderColor: "rgba(221, 211, 211, 1)",
        borderWidth: 1,
        formatter: function(itemObject) {

          var tooltipString = '<div class="series-item-title">' + itemObject.name + '</div>';

          if (chartDataConversion.show === true) {

          }

          return tooltipString + '<div style="margin-bottom: 5px"></div>' + itemObject.marker + itemObject
            .value;

        }

      };

      //词云图
      if (selected_chart_type === 'C230') {

        //图表标题配置
        var titleOption = {};

        var seriesOption = [{
          name: yAxisItem.newName,
          type: 'pie',
          radius: '70%',
          center: ['50%', '50%'],
          label: {
            show: chartLabelShow
          },
          data: seriesDataListOption
        }];

      } else if (selected_chart_type === 'C288') {

        //图表标题配置
        var titleOption = {
          show: chartText.show,
          text: yAxisItem.newName,
          left: 'center',
          top: 'center',
          textStyle: {
            fontWeight: 'bolder',
            fontSize: chartText.text_styles.font_size,
            color: chartText.text_styles.color
          }
        };

        var seriesOption = [{
          name: yAxisItem.newName,
          type: 'pie',
          selectedMode: 'single',
          radius: ['55%', '70%'],
          label: {
            show: chartLabelShow
          },
          data: seriesDataListOption
        }];

      }

      //图表配置信息
      var echartOption = {
        color: colorList,
        title: titleOption,
        legend: legendDataOption,
        tooltip: tooltipOption,
        series: seriesOption
      };

      return {
        echartOption: echartOption,
        canChartConfig: {}
      };

    },
  },


}

export default EchartsWordCloudParseModule



