<template>
  <!--可视化组件视图-->
  <div class="chart-box-view chart-design-box-view" id="export-chart-box">
    <!--无数据提示-->
    <div class="chart-data-empty" v-show="chartDataEmptyCalc">
      <span>当前分析结果数据为空，请添加数据字段！</span>
    </div>
    <!--组件数据加载动画-->
    <div class="loading-box design-chart-loading" v-show="$store.state.chartLoadingAnimation">
      <div class="data-loading-animation"></div>
    </div>
    <!--表格组件视图-->
    <div class="grid-compoment-view" v-show="$store.state.defaultChartConfig.select_type === 'C200'">
      <el-table :data="$store.state.dataSetListLimit" stripe border :height="dataTableHeightStyle" size="small"
        :header-cell-style="dataTableHeaderStyle" id="data-table">
        <el-table-column label="序号" width="70px">
          <template slot-scope="scope">{{scope.$index+1}}</template>
        </el-table-column>
        <el-table-column min-width="180" sortable v-for="(item, index) in $store.state.tableHeader" :key="index"
          :label="item.newName" :prop="item.name" align="center" />
      </el-table>
    </div>
    <!--图表组件视图-->
    <div class="chart-compoment-view" v-show="$store.state.defaultChartConfig.select_type !== 'C200'">
      <div id="render-chart-box"></div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'chartsRender',
    data() {
      return {
        dataTableHeaderStyle: {
          background: '#F5F7FA',
          color: '#606266'
        },
        dataSetList: [],
        chartLoadingAnimation: false,
        dataTableHeightStyle: document.documentElement.clientHeight - 194
      }
    },
    mounted() {



    },

    computed: {
      //state状态更新且子组件状态未同步时,用computed
      tableHeader() {

        return this.$store.state.tableHeader;

      },
      //就算数据是否为空
      chartDataEmptyCalc() {

        var selectedFieldList = JSON.parse(JSON.stringify(this.$store.state.defaultChartConfig.analysis_column));

        if (selectedFieldList.data_2.length === 0 &&
          selectedFieldList.data_4.length === 0 &&
          selectedFieldList.data_4.length === 0) {

          this.chartDataEmpty = true;

        } else {

          this.chartDataEmpty = false;
        }

        return this.chartDataEmpty;

      },

    },
    methods: {

    }
  }
</script>
<style scoped>
  .chart-compoment-view {
    height: 100%;
    width: 100%;
    position: relative;
  }

  #render-chart-box {
    height: 100%;
    width: 100%;
    position: relative;
  }

  .design-chart-loading {
    background: #fff;
    left: 0px;
    right: 1px;
  }

  .loading-box {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 30;
  }

  .data-loading-animation {
    width: 60px;
    height: 60px;
    position: absolute;
    margin: -30px 0 0 -30px;
    background: url(data:image/svg+xml;base64,) no-repeat;
    background-size: 100%;
    left: 50%;
    top: 50%;
    z-index: 999;
  }
</style>



