import Vue from 'vue'

import App from './App'

import VueResource from 'vue-resource'

import VueRouter from 'vue-router'

import routes from './route/RouteConfig.js'

const router = new VueRouter
({
    mode: 'hash',
    routes
})
export default router;


import Vuex from 'vuex'

import MainStates from './store/MainStates';

import MainMutations from './store/MainMutations';

import MainGetters from './store/MainGetters';

import MainActions from './store/MainActions';


import './assets/libs/theme/index.css'

import ElementUI from 'element-ui'

import Echarts from 'echarts'

import _ from 'lodash'

// import 'echarts-wordcloud'

// import 'echarts-liquidfill'

import Html2canvas from 'html2canvas'

import FileSaver from 'file-saver'

import XLSX from 'xlsx'


import './assets/css/global.css'

import './assets/iconfont/iconfont.css'

import './assets/iconfont/iconfont.js'


Vue.config.productionTip = false

Vue.use(VueRouter)

Vue.use(VueResource)

Vue.use(Vuex)

Vue.use(ElementUI, {
  size: 'small',
  zIndex: 3000
});

Vue.prototype.$echarts = Echarts

Vue.prototype.$html2canvas = Html2canvas

Vue.prototype.$XLSX = XLSX

Vue.prototype.$FileSaver = FileSaver

Vue.prototype._ = _

//Vuex 公共状态管理
var store = new Vuex.Store({

  //主(模块)状态管理器
  //这里相当于Vue实例中的data,用于存放公共数据
  state: MainStates,

  // 这里相当于Vue实例中的methods,用于定义方法所有的方法
  mutations: MainMutations,

  //这里可以监听state的值 直接返回出去 只读取值,如果需要修改值找mutations,需要return出去
  getters: MainGetters,

  //公共HTTP请求函数方法
  actions: MainActions,

  modules: {},

})


new Vue({
  el: '#app',
  components: {
    App
  },
  store: store,
  router:router,
  template: '<App/>'
})



