const DataTableParseModule = {

  state: {

  },

  mutations: {
    //表格类型图配置解析
    dataTableParseModuleOptionInit(state) {

      //数据显示条目数
      var echartLimitNum = state.defaultChartConfig.limit.num;

      var dataSetListOption = JSON.parse(JSON.stringify(state.dataSetList));

      var selectedFieldList = JSON.parse(JSON.stringify(state.defaultChartConfig.analysis_column));

      var analysisFieldsList = [];

      var countIndex = 0;

      for (var idx in selectedFieldList.data_2) {

        var dimensionItem = selectedFieldList.data_2[idx];

        analysisFieldsList.push(({

          name: dimensionItem.name,
          newName: dimensionItem.newName,
          index: countIndex,
          type: dimensionItem.type

        }));

        countIndex++

      }

      for (var idx in selectedFieldList.data_4) {

        var valueOneItem = selectedFieldList.data_4[idx];

        analysisFieldsList.push(({

          name: valueOneItem.name,
          newName: valueOneItem.newName,
          index: countIndex,
          type: valueOneItem.type

        }));

        countIndex++

      }

      for (var idx in selectedFieldList.data_5) {

        var valueTwoItem = selectedFieldList.data_5[idx];

        analysisFieldsList.push(({

          name: valueTwoItem.name,
          newName: valueTwoItem.newName,
          index: countIndex,
          type: valueTwoItem.type

        }));

        countIndex++

      }


      // 条目数限制处理
      if (state.defaultChartConfig.limit.show === true) {

        if (state.defaultChartConfig.limit.type === 'before') {

          dataSetListOption = dataSetListOption.splice(0, echartLimitNum);

        } else {

          dataSetListOption = dataSetListOption.splice(dataSetListOption.length - echartLimitNum, dataSetListOption
            .length - 1);

        }

      }

      return {
        dataTableOption: analysisFieldsList,
        dataSetList: dataSetListOption,
        canChartConfig: {}
      };

    },
  },

  getters: {

  },


}

export default DataTableParseModule



