<template>
  <div class="login-box">
    <div id="login-bg"></div>
    <div class="systemName">
      <h1>商图BI</h1>
      <p>在线数据分析工具</p>
    </div>
    <div class="imgBox">
      <div class="left-image"></div>
    </div>
    <div class="loginBox">
      <div class="loginContent">
        <p class="loginName">登录</p>

        <el-form :model="userInfoForm" status-icon :rules="userInfoRules" ref="userInfoForm" class="login-Form">

          <div class="login-user-item">
            <el-form-item label="" prop="username">
              <el-input prefix-icon="el-icon-user" size="medium" placeholder="用户名 : user"
                v-model="userInfoForm.username" autocomplete="off">
              </el-input>
            </el-form-item>
          </div>

          <div class="login-user-item">
            <el-form-item label="" prop="password">
              <el-input prefix-icon="el-icon-lock" type="password" size="medium" placeholder="密码 : 123456"
                v-model="userInfoForm.password" autocomplete="off">
              </el-input>
            </el-form-item>
          </div>

          <div class="login-user-item">
            <el-checkbox v-model="userInfoForm.checked">自动登录</el-checkbox>
          </div>
          <div class="login-user-item login-btn">
            <el-button size="medium" type="primary" @click="loginSubmit('userInfoForm')">登录</el-button>
          </div>
        </el-form>
      </div>
    </div>
    <div class="footer-item">Copyright © guanrenchao.com Co., Ltd </div>
  </div>
</template>
<script>
  export default {
    name: 'LoginPage',
    components: {},
    data() {

      var validateUserName = (rule, value, callback) => {

        if (value === '') {

          callback(new Error('请输入用户名'));

        } else if (value !== 'user') {

          callback(new Error('不存在此用户名!'));

        } else {

          callback();

        }

      };

      var validatePassWord = (rule, value, callback) => {

        if (value === '') {

          callback(new Error('请再次输入密码'));

        } else if (value !== '123456') {

          callback(new Error('密码错误!'));

        } else {

          callback();

        }

      };
      return {

        userInfoForm: {
          username: 'user',
          password: '123456',
          checked: true
        },
        userInfoRules: {
          username: [{
            validator: validateUserName,
            trigger: 'blur'
          }],
          password: [{
            validator: validatePassWord,
            trigger: 'blur'
          }],
        },
      }
    },
    //(创建后)
    created() {


    },
    //钩子(载入后)
    mounted() {

    },
    computed: {
      //计算侦听
      //state状态更新且子组件状态未同步时,用computed

    },
    //函数列表
    methods: {

      //登录方法
      loginSubmit(userInfoForm) {

        this.$refs[userInfoForm].validate((valid) => {

          console.log(valid)

          if (valid) {

            this.$message({
              message: '登录成功！',
              type: 'success'
            });

            setTimeout(() => {

              this.$router.push({
                path: 'home'
              });

            }, 1500);

          } else {

            this.$message({
              message: '登录失败！',
              type: 'error'
            });

            return false;
          }
        });

      },

    },

  }
</script>
<style scoped>
  .systemName {
    position: absolute;
    top: 8%;
    left: 300px;
    color: #fff;
    letter-spacing: 6px;
  }

  .systemName h1 {
    font-size: 50px;
    margin-bottom: 20px;
  }

  .systemName p {
    font-size: 30px;
    font-weight: 700;
  }

  .imgBox {
    position: absolute;
    top: 50%;
    left: 15%;
    margin: -200px 0 0 0;
    width: 450px;
    height: 450px;
  }

  .left-image {
    height: 100%;
    width: 100%;
    background: url(../../assets/images/login-bg.svg) no-repeat;
    background-size: 100%;
  }

  .loginBox {
    position: absolute;
    width: 320px;
    background-color: #fff;
    top: 50%;
    right: 150px;
    margin: -280px 0 0 0;
    box-shadow: 0 15px 30px 0 rgba(255, 255, 255, .15) inset;
  }

  .loginContent {
    padding: 0 25px 20px;
    height: 380px;
  }

  .loginName {
    font-size: 30px;
    height: 80px;
    line-height: 80px;
    margin: 0;
    font-weight: 700;
    letter-spacing: 5px;
    color: #555;
  }

  .login-user-item {
    margin: 35px 0 0;
  }

  .login-btn button {
    width: 100%;
  }

  .login-btn button span {
    font-size: 15px;
    letter-spacing: 2px;
  }

  .footer-item {
    width: 100%;
    position: absolute;
    bottom: 20px;
    font-size: 14px;
    text-align: center;
    line-height: 30px;
    color: #fff;
  }

  #login-bg {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(135deg, rgb(226, 176, 255) 10%, rgb(159, 68, 211) 100%);
  }
</style>



