const EchartsWordCloudParseModule = {

  state: {

  },

  mutations: {
    //监听图表配置函数
    radarParseOptionInit(state, colorList) {

      //数据集
      var dataSetList = JSON.parse(JSON.stringify(state.dataSetList));

      var defaultChartConfig = JSON.parse(JSON.stringify(state.defaultChartConfig));

      //所选图表类型
      var selected_chart_type = defaultChartConfig.select_type;

      //图表标题配置
      var chartText = defaultChartConfig.echart_config.chart_text;

      //图表数据转换配置
      var chartDataConversion = defaultChartConfig.echart_config.chart_data_conversion;

      //图表悬浮提示
      var chartTips = defaultChartConfig.echart_config.chart_tips;

      //图表图例配置
      var chartLegend = defaultChartConfig.echart_config.chart_legend;

      //图表主题
      var mainTheme = defaultChartConfig.chart_theme.main_theme;

      //获取已选字段 X轴 第一个字段
      var xAxisItem = defaultChartConfig.analysis_column.data_2[0];

      //获取已选字段 Y轴 第一个字段
      var yAxisItem = defaultChartConfig.analysis_column.data_4;

      //数据显示条目数
      var dataSetListLimit = defaultChartConfig.limit;

      var dataSetLimitNum = parseInt(defaultChartConfig.limit.num);

      if (dataSetListLimit.show === true) {

        if (dataSetList.length >= dataSetLimitNum) {

          if (defaultChartConfig.limit.type === 'before') {

            dataSetList = dataSetList.splice(0, dataSetLimitNum);

          } else {

            dataSetList = dataSetList.splice(dataSetList.length - dataSetLimitNum, dataSetList.length - 1);

          }
        }

      }


      if (mainTheme === "#fff") {

        var fontColor = "#2c2c3d";

      } else {

        var fontColor = "#fff";

      }

      //图表标题配置
      var titleOption = {
        show: chartText.show,
        text: yAxisItem.newName,
        left: chartText.horizontal,
        top: chartText.vertical,
        textStyle: {
          fontWeight: 'bolder',
          fontSize: chartText.text_styles.font_size,
          color: chartText.text_styles.color
        }
      };

      //图表图例数据列表
      var legendDataOption = {
        icon: 'circle',
        type: 'scroll',
        itemHeight: 10,
        itemWidth: 10,
        show: chartLegend.show,
        left: chartLegend.horizontal,
        top: chartLegend.vertical,
        data: [],
        textStyle: {
          color: fontColor
        }
      };

      var seriesOption = [{
        type: 'radar',
        data: []
      }];

      //雷达图系列数据配置
      var radarOption = {
        shape: 'circle',
        scale: true,
        indicator: [],
        center: ['50%', '50%'],
        radius: '80%'
      };

      //图表悬浮提示配置
      var tooltipOption = {
        show: chartTips.show,
        backgroundColor: '#fff',
        padding: 10,
        trigger: "item",
        textStyle: {
          color: "#666"
        },
        borderColor: "rgba(221, 211, 211, 1)",
        borderWidth: 1,
        formatter: function(item) {

          var tooltipString = '<div class="series-item-title">' + item.name + '</div>';

          for (var index in item.value) {

            tooltipString += '<div style="margin-bottom: 5px"></div>' + item.marker + yAxisItem[index].newName +
              " : " + item.value[index];

          }

          // if (chartDataConversion.show === true) {

          // }

          return tooltipString;

        }
      };

      //图表数据装载解析
      for (var idx in dataSetList) {

        var rowItem = dataSetList[idx];

        // 所有的 X 轴字段解析(第一个字段)
        legendDataOption.data.push(rowItem[xAxisItem.name]);

        var seriesDataItem = {
          name: rowItem[xAxisItem.name],
          value: []
        };

        // 所有的 Y 轴字段解析并装载数据
        for (var valueOneIndex in yAxisItem) {

          var valueOneItem = yAxisItem[valueOneIndex];

          seriesDataItem.value.push(rowItem[valueOneItem.name]);

        }

        seriesOption[0].data.push(seriesDataItem);

      }

      //获取雷达图考察值(极坐标最大值)
      var indicatorMaxValue = EchartsWordCloudParseModule.mutations.getMaxValue(seriesOption[0].data);

      // 所有的数值一字段解析并装载数据
      for (var valueOneIndex in yAxisItem) {

        var valueOneItem = yAxisItem[valueOneIndex];

        var indicatorObject = {
          text: valueOneItem.newName,
          max: indicatorMaxValue
        };

        radarOption.indicator.push(indicatorObject);

      }

      //图表配置信息
      var echartOption = {
        color: colorList,
        title: titleOption,
        tooltip: tooltipOption,
        legend: legendDataOption,
        radar: radarOption,
        series: seriesOption,
      };

      return {
        echartOption: echartOption,
        canChartConfig: {}
      };

    },
    //获取数据列表中的最大值
    getMaxValue(valueList) {

      var maxValue = valueList[0].value[0];

      for (var idx in valueList) {

        var valueArray = valueList[idx].value;

        for (var valueIndex in valueArray) {

          var valueItem = valueArray[valueIndex];

          maxValue = valueItem > maxValue ? valueItem : maxValue;
        }

      }

      return maxValue;

    }
  },


}

export default EchartsWordCloudParseModule



