// 2. 定义路由
// 每个路由应该映射一个组件。 其中"component" 可以是
// 通过 Vue.extend() 创建的组件构造器，
// 或者，只是一个组件配置对象。

import HomePage from '../components/pages/HomePage.vue'

import LoginPage from '../components/pages/LoginPage.vue'

const routes = [{
    path: '/home',
    name: 'home',
    component: HomePage
  },
  {
    path: '/login',
    name: 'login',
    component: LoginPage
  },
  {
    path: '/',
    component: LoginPage
  }
];

export default routes



