<template>
  <!--顶部导航视图视图-->
  <div class="header-navi">
    <div class="header-navi-left float-left">
      <i class="el-icon-data-analysis float-left"></i>
      <span class="float-left">商图BI</span>
    </div>
    <div class="header-navi-right float-right">
      <ul>
        <li>
          <el-dropdown>
            <span class="el-dropdown-link">
              <div class="user-info">
                <i class="el-icon-user"></i>
                <span>Serati Ma</span>
              </div>
            </span>
            <el-dropdown-menu class="user-info-right" slot="dropdown">
              <el-dropdown-item>
                <div>
                  <i class="el-icon-user"></i>
                  <span> 个人中心</span>
                </div>
              </el-dropdown-item>
              <el-dropdown-item>
                <div>
                  <i class="el-icon-setting"></i>
                  <span> 个人设置</span>
                </div>
              </el-dropdown-item>
              <el-dropdown-item>
                <div @click="logout">
                  <i class="iconfont icon-logout"></i>
                  <span>退出登录</span>
                </div>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'HeaderNavi',
    data() {
      return {


      }
    },
    mounted() {



    },

    computed: {
      //state状态更新且子组件状态未同步时,用computed


    },
    methods: {

      logout() {

        console.log("2")

        this.$message({
          message: '退出成功！',
          type: 'success'
        });

        setTimeout(() => {

          this.$router.push({
            path: 'login'
          });

        }, 1500);

      },


    }
  }
</script>
<style scoped>
  .header-navi {
    height: 48px;
    line-height: 48px;
    width: calc(100% - 32px);
    z-index: 990;
    position: fixed;
    padding: 0 16px;
    background: #fff;
    -webkit-box-shadow: 0 1px 4px rgb(0 21 41 / 8%);
    box-shadow: 0 1px 4px rgb(0 21 41 / 8%);
  }

  .user-info {
    padding: 0 10px;
    cursor: pointer;
  }

  .user-info>i {
    font-size: 18px;
    padding: 0 8px;
  }

  .user-info-right li {
    height: 37px;
    line-height: 37px;
    padding: 0;
  }

  .user-info-right li div {
    padding: 0 15px;
  }

  .user-info-right i {
    font-size: 15px;
  }

  .user-info:hover {
    background: rgba(0, 0, 0, .025);
  }

  .header-navi-left i {
    font-size: 28px;
    line-height: 49px;
    height: 49px;
    color: #984fff;
  }

  .header-navi-left span {
    height: 49px;
    line-height: 49px;
    display: inline-block;
    padding: 0 0 0 8px;
    font-size: 20px;
    font-weight: 700;
    letter-spacing: 2px;
    color: #888;
  }
</style>



